.App {
  /* text-align: center; */
  background:linear-gradient(#7CB9E8,#9BC3E5,#B6CEE5);
  height: 100vh;
}
.empform{
  position: relative;
  top: 50px;
  box-shadow: 1px 1px 60px #252525; 
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content-heading h1{
  margin-top: 200px;
  font-size: 50px;
}
.name-login{
  padding-top: 150px;
}